.chatMainContainer {
	width: 100vw;
	height: 100vh;
	max-width: 100vw;
	overflow-y: hidden;
	overflow-x: hidden;
	/* background-color: black; */

	display: flex;
	flex-direction: column;
	flex-flow: column;
	justify-content: space-between;
}

.viewControlsContainer {
	width: 100%;
	height: 75%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding-bottom: 20px;
}
@media (max-width: 700px) {
	.viewControlsContainer {
		height: 70%;
	}
}

@media screen and (orientation: portrait) {
	.chatMainContainer {
		transform: rotate(-90deg);
		transform-origin: left top;
		width: 100vh;
		min-width: 100vh;
		height: 100vw;
		min-height: 100vw;
		position: absolute;
		top: 100%;
		left: 0;
	}
}
