.mainKeyboardContainer {
	height: 25%;
	width: 100vw;
	overflow-x: scroll;
	overflow-y: hidden;
	transform: rotateX(180deg);
}
@media (max-width: 700px) {
	.mainKeyboardContainer {
		height: 30%;
	}
}
@media screen and (orientation: portrait) {
	.mainKeyboardContainer {
		width: 100vh;
		height: 30vw;
	}
}

.mainKeyboardContainer::-webkit-scrollbar {
	width: 100vw;
	height: 8px;
}
.mainKeyboardContainer::-webkit-scrollbar-track {
	background-color: #7b35b7;
}
.mainKeyboardContainer::-webkit-scrollbar-thumb {
	background-color: #511e7c;
	border-radius: 7px;
}
.keyboardContainer {
	transform: rotateX(180deg);
	width: fit-content;
	height: 100%;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: center;
	margin: auto;
	/* border-radius: 20px 20px 0px 0px ; */
	overflow: hidden;
}
