.whiteKey {
	background-color: #2c2d6e;
	border: solid #000d59 1px;
	width: 60px;
	height: 100%;
	cursor: pointer;
	position: relative;
}
.whiteKey:focus {
	outline: none;
}
.eighth {
	position: absolute;
	top: 5px;
	left: 10px;
}
.keyName {
	position: absolute;
	bottom: 10px;
	width: 100%;
	left: 50%;
	margin-left: -50%;
}
@media (max-width: 1000px) {
	.keyName {
		display: none;
	}
}
.blackKey {
	--width: 40px;
	background-color: #7892b4;
	border: none;
	border-radius: 0px 0px 5px 5px;
	width: var(--width);
	height: 50%;
	z-index: 2;
	margin-right: calc(-1 * (var(--width) / 2));
	margin-left: calc(-1 * (var(--width) / 2));
	cursor: pointer;
	position: relative;
}
.blackKey:focus {
	outline: none;
}
.blackKey span {
	color: hsl(240, 32%, 15%);
	font-weight: lighter;
	font-style: italic;
	/* margin-bottom: 15px; */
}
